.App {
    text-align: center;
}




.App-header {
    background-color: #529A86;
    height: 5rem;
    padding: 2rem;
    color: white;
}


.App-title {
    font-size: 2em;
}



@import url(https://fonts.googleapis.com/css?family=Roboto:300);

.login-page {
    width: 360px;
    padding: 8% 0 0;
    margin: auto;
}
.form {
    position: relative;
    z-index: 1;
    background: #FFFFFF;
    max-width: 360px;
    margin: 0 auto 100px;
    padding: 45px;
    text-align: center;
    -webkit-box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
            box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);

}
.form input {
    font-family: "Roboto", sans-serif;
    outline: 0;
    background: #f2f2f2;
    width: 100%;
    border: 0;
    margin: 0 0 15px;
    padding: 15px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-size: 14px;
}
.form button {
    font-family: "Roboto", sans-serif;
    text-transform: uppercase;
    outline: 0;
    background: #4CAF50;
    width: 100%;
    border: 0;
    padding: 15px;
    color: #FFFFFF;
    font-size: 14px;
    -webkit-transition: all 0.3 ease;
    -o-transition: all 0.3 ease;
    transition: all 0.3 ease;
    cursor: pointer;
}
.form button:hover,.form button:active,.form button:focus {
    background: #43A047;
}
.form .message {
    margin: 15px 0 0;
    color: #b3b3b3;
    font-size: 12px;
}
.form .message a {
    color: #4CAF50;
    text-decoration: none;
}
.register-form {
    visibility: hidden;
    height: 0;
}
.register-form.show {
    visibility: visible;
    height: 200px;

}
#login-form:target {
    -webkit-transition: visibility 0.3s linear 300ms, opacity 300ms,  height 1000ms;
    -o-transition: visibility 0.3s linear 300ms, opacity 300ms,  height 1000ms;
    transition: visibility 0.3s linear 300ms, opacity 300ms,  height 1000ms;
    visibility: hidden;
    opacity: 0;
    height: 0;
}
#login-form{

}

#register-form:target {

    -webkit-transition: visibility 0.3s linear 300ms, opacity 300ms, height 1000ms;

    -o-transition: visibility 0.3s linear 300ms, opacity 300ms, height 1000ms;

    transition: visibility 0.3s linear 300ms, opacity 300ms, height 1000ms;
    opacity: 0;
    visibility: hidden;
    height: 0;

}
#register-form{
    visibility: visible;
    opacity: 1.0;
    height: 200px;


}


