    background: -webkit-gradient(linear, right top, left top, from(#76b852), to(#8DC26F));
    background: linear-gradient(to left, #76b852, #8DC26F);

    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
